export default function Result({ data }: { data: any }) {
  return (
    <div>
      <h2>Analysis Result:</h2>
      <p><strong>Original Answer:</strong> {data.original_answer}</p>
      <p><strong>Concepts:</strong> {data.concepts.join(', ')}</p>
      <p><strong>Clarity Score:</strong> {data.clarity}</p>
    </div>
  )
}
